.home__slider-front {
  width: 100%;
  height: 100vh;
  min-height: 100vh;
  max-height: 100vh;
  position: relative;
  transform: translateY(-70px);
  background-image: url("https://images.pexels.com/photos/1640777/pexels-photo-1640777.jpeg?auto=compress&cs=tinysrgb&h=750&w=1260");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  user-select: none;
}

.sliderPostal__title {
  margin-bottom: 10px;
}

.sliderFront__sidebox {
  position: absolute;
  left: 20px;
  top: 110px;
  background: #d70f64;
  width: 180px;
  height: 180px;
  min-width: 200px;
  min-height: 200px;
  max-height: 200px;
  max-width: 200px;
  flex: 0.2;
  color: white;
  padding: 10px;
}

.sliderFront__sidebox-line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 8px 0;
}

.sliderFront__sidebox-line span {
  font-size: 13px;
  font-family: "Montserrat";
}

.sliderFront__sidebox-btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.sliderFront__order-btn {
  padding: 6px 15px;
  margin-top: 10px;
  cursor: pointer;
  font-weight: bold;
  letter-spacing: 1px;
  border: none;
  transition: all 0.15s linear;
  background: white;
  color: black;
}

.sliderFront__order-btn:hover {
  background: dodgerblue;
  color: white;
}

.home__address-postal-field {
  margin: 0 auto;
  height: calc(100vh);
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.sliderPostalCode__field {
  width: 35%;
  display: flex;
  justify-content: space-between;
  background: white;
  padding: 10px 20px;
}

.sliderPostalCode__input-field {
  padding: 10px 5px;
  flex: 0.8;
  border: 1px solid lightgray;
  font-size: 15px;
  outline: none;
  font-weight: bold;
  letter-spacing: 1px;
}

.sliderPostalCode__find-btn {
  background: white;
  padding: 10px 30px;
  flex: 0.15;
  border: none;
  font-size: large;
  font-weight: bold;
  background: rgb(248, 10, 111);
  color: white;
  letter-spacing: 1.1px;
  cursor: pointer;
}

.sliderPostalCode__find-btn:hover {
  background: rgb(255 0 141);
}

.home__address-field {
  margin: 0 auto;
  flex: 1;
  display: none;
  height: calc(100vh);
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.slider__title {
  margin-top: -75px;
  margin-bottom: 20px;
}

.home__location-buttons {
  width: 40%;
  display: flex;
  justify-content: space-evenly;
}

.home__location-buttons-link {
  width: 40%;
  transition: all 0.2s ease;
}

.home__location-buttons-link:active {
  transform: translateY(4px);
  background: #e70664;
}

.home__location-buttons-button {
  width: 100%;
  background: rgb(248, 10, 111);
  padding: 10px 20px;
  color: white;
  border: none;
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.home__location-buttons-button:hover {
  background: rgb(255 0 141);
}

.home__location-buttons-button {
  color: white;
  font-size: medium;
  font-weight: 600;
  letter-spacing: 0.2px;
  font-family: "Montserrat";
}

@media only screen and (max-width: 1100px) {
  .home__location {
    width: 45%;
  }
  .home__location-buttons {
    width: 45%;
  }

  .sliderPostalCode__field {
    width: 40%;
  }
}

@media only screen and (max-width: 1000px) {
  .home__address-field {
    position: relative;
    display: block;
    top: 200px;
  }

  .home__location {
    width: 50%;
    margin-bottom: 40px;
  }

  .home__location-buttons {
    width: 50%;
  }

  .sliderPostalCode__field {
    width: 45%;
  }
}

@media only screen and (max-width: 800px) {
  .sliderPostalCode__field {
    width: 55%;
  }

  .home__location {
    width: 60%;
  }
  .home__location-buttons {
    width: 60%;
  }

  .sliderFront__sidebox {
    left: 50%;
    top: 110px;
    transform: translate(-50%, 0);
  }
}

@media only screen and (max-width: 700px) {
  .sliderPostalCode__field {
    width: 80%;
  }

  .home__address-field {
    width: 90%;
    max-width: 95%;
  }

  .home__location {
    flex-direction: column;
    width: 90%;
  }

  .home__location-buttons {
    width: 90%;
  }

  .location__field:focus {
    transform: scale(1);
  }

  .location__field-btn {
    width: 100%;
    margin: 10px 0;
  }

  .location__field-btn:hover {
    transform: scale(1.02);
  }
}

@media only screen and (max-width: 600px) {
  .sliderPostalCode__field {
    width: 80%;
  }

  .home__location-buttons {
    width: 100%;
  }
}

@media only screen and (max-width: 430px) {
  .home__location-buttons {
    width: 100%;
    flex-direction: column;
    align-items: center;
  }

  .home__location-buttons-link {
    width: calc(100% - 30px);
    margin: 6px 0;
  }
}
